






import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import { formatDate } from '@/until/index'

@Component({
  components: {

  },
  filters: {
   
  }
})
export default class houseSelect extends Vue {
   @Prop({ default: '' }) readonly url!: string
  get imgUrl () {
    if (this.url && typeof this.url === 'string') {
      return [{image: this.url}]
    } else if (Array.isArray(this.url)){
      let imgArr = []
      this.url.forEach((item)=>{
        imgArr.push({image: item})
      })
      return imgArr
    } else {
      return []
    }
  }
}
